export default {
	data() {
		return {
			orderData: null
		}
	},
	onLoad(options) {
		let params = {
			orderNumber: options.id
		}
		this.loadData(params);
	},
	methods: {
		async loadData(params){ 
			const { data } = await this.Api.orderDetails(params);
			this.orderData = data;
		}
	}
}
